<template>
  <div>
    <form @submit.prevent="addProduct">
      <input type="text" v-model="name" placeholder="Nom du produit">
      <input type="text" v-model="image" placeholder="URL de l'image">
      <input type="text" v-model="price" placeholder="Prix du produit">
      <select v-model="category">
        <option disabled value="">Veuillez sélectionner une catégorie</option>
        <option>Jeans</option>
        <option>T-shirt</option>
        <option>Chaussette</option>
        <option>Chaussure</option>
        <!-- Ajoutez d'autres options de catégorie ici -->
      </select>
      <button type="submit">Ajouter le produit</button>
    </form>

    <!-- Boutons de catégorie -->
    <div>
      <button v-for="category in categories" :key="category" @click="fetchProducts(category)">
        {{ category }}
      </button>
    </div>

    <!-- Carrousel de produits -->
    <div ref="productsSection">
      <div v-for="product in products" :key="product.name">
        <img :src="product.image" :alt="product.name" class="product-image">
        <div>{{ product.name }}</div>
        <div>{{ product.price }} €</div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, nextTick } from 'vue'
import { getDatabase, ref as dbRef, set, onValue } from "firebase/database";

export default {
  setup() {
    const name = ref('');
    const image = ref('');
    const price = ref('');
    const category = ref(''); // Ajout d'une référence pour la catégorie
    const categories = ref(['Jeans', 'T-shirt', 'Chaussette', 'Chaussure']);
    const products = ref([]);

    const addProduct = async () => {
      // Obtenir une référence à la base de données Firebase
      const db = getDatabase();

      // Créer un nouvel objet dans Firebase avec les détails du produit
      const productRef = dbRef(db, `products/${category.value}/${name.value}`); // Utilisation de la catégorie comme clé parente
      set(productRef, {
        name: name.value,
        image: image.value,
        price: price.value
      });
    };

    const fetchProducts = async (category) => {
      const db = getDatabase();
      const productRef = dbRef(db, `products/${category}`);
      onValue(productRef, (snapshot) => {
        products.value = snapshot.val();
      });

      // Scroll to the products section after fetching the products
      nextTick(() => {
        const firstProductImage = document.querySelector('.product-image');
        if (firstProductImage) {
          firstProductImage.scrollIntoView({ behavior: 'smooth' });
        }
      });
    };
    return {name, image, price, category, categories, products, addProduct, fetchProducts}; // Ajout de la catégorie aux propriétés retournées
  }
};
</script>

<style scoped>
.product-image {
  max-width: 100%;
  max-height: 300px;
  object-fit: cover;
}
</style>